import _ from 'lodash';
import React, { Component } from 'react';
import { Col, Row } from 'reactstrap';
import ReactSlickCarousel from 'components/Templates/ReactSlickCarousel/ReactSlickCarousel';
import { LazyLoadImage } from 'react-lazy-load-image-component';

import PhoneShell from 'assets/images/Landing/Phone.png';
import Magician from 'assets/images/Landing/MagicianNew.svg';
import AppStoreBadge from 'assets/images/app-store-badge.png';
import PlayStoreBadge from 'assets/images/google-play-badge.png';

// import 'react-lazy-load-image-component/src/effects/opacity.css';

import { appData } from '../Landing.constants';

import './LandingApp.scss';

class LandingApp extends Component {
  renderPhone() {
    return (
      <div className="phone-container">
        <LazyLoadImage
          alt="phone-shell.png"
          src={PhoneShell}
          // effect="opacity"
          className="phone-shell"
        />
        <ReactSlickCarousel
          vertical
          lazyLoad
          // autoplay
          dots
          slidesToShow={1}
          arrows={false}
          className="green-dots vertical-dots"
        >
          {
            _.map(appData, (data, index) => (
              // <LazyLoadImage
              //   key={`phone-img-${index}`}
              //   className="phone-img"
              //   src={data.src}
              //   alt={data.alt}
              //   effect="opacity"
              // />
              <img
                key={`phone-img-${index}`}
                className="phone-img"
                src={data.src}
                alt={data.alt}
              />
            ))}
        </ReactSlickCarousel>
      </div>
    );
  }

  renderMagician() {
    return (
      <div className="magician-container">
        <LazyLoadImage
          src={Magician}
          alt="Magician.svg"
          // effect="opacity"
        />
      </div>
    );
  }

  renderAppInfo() {
    return (
      <Row className="w-100 d-flex flex-column" noGutters>
        <Col xs={12} className="magic-paragraph">
          <h2>The <span>Magic</span> is in our app</h2>
          <p className="app-desc">
            All your insurance needs at your fingertips. Get covered on
            the go, manage all your insurance matters in a single app.
          </p>
        </Col>
        <Col xs={12} className="store-container">
          <h3>Download Now</h3>
          <Row noGutters>
            <Col className="d-flex d-lg-block justify-content-center" xs={12} sm={6}>
              <a href="https://itunes.apple.com/sg/app/policypal/id1118511972?mt=8">
                <LazyLoadImage
                  src={AppStoreBadge}
                  alt="appstore.jpg"
                  className="store-btn"
                  // effect="opacity"
                />
              </a>
            </Col>
            <Col className="d-flex d-lg-block justify-content-center" xs={12} sm={6}>
              <a href="https://play.google.com/store/apps/details?id=com.idvsgjo31w9m05f6raeb3&amp;hl=en">
                <LazyLoadImage
                  src={PlayStoreBadge}
                  alt="playstore.jpg"
                  className="store-btn"
                  // effect="opacity"
                />
              </a>
            </Col>
          </Row>
        </Col>
      </Row>
    );
  }

  render() {
    return (
      <Row className="landing-app-container" noGutters>
        <Col className="my-auto" xs={12}>
          <Row className="w-100" noGutters>
            <Col className="d-none d-lg-block" xs={1} />
            <Col className="left-container" xs={12} lg={5}>
              {this.renderPhone()}
              {this.renderMagician()}
            </Col>
            <Col className="right-container" xs={12} lg={5}>
              {this.renderAppInfo()}
            </Col>
          </Row>
        </Col>
      </Row>
    );
  }
}

export default LandingApp;
